html {
  font-size: 100%;
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
}

ol,
ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: unset;
}

:root {
  --paddingSides-content: 40px;
}
@media (max-width: 63.9375em) {
  :root {
    --paddingSides-content: 20px;
  }
}

.app {
  display: flex;
}

.nav {
  background-color: #191718;
}
.nav__link {
  position: relative;
  display: block;
  min-width: 150px;
  padding: 14px 0 11px 16px;
  font-size: 16px;
  font-weight: 700;
  color: #aaaaaa;
  line-height: 18.75px;
}
.nav__link::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #5c5c5c;
}
.nav__link.active {
  background: #5c5c5c;
  color: #fff;
  border-bottom: none;
}
.nav__link.home {
  color: #fff;
}

.main {
  padding-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px var(--paddingSides-content);
}
.main__logo {
  width: 310px;
  height: 122px;
  margin-bottom: 30px;
}
.main__logo > img {
  object-fit: contain;
  max-width: 100%;
  max-height: 100%;
}
@media (max-width: 46.1875em) {
  .main__logo {
    height: 68px;
    width: auto;
  }
}
.main__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 38px 0;
  margin-bottom: 40px;
  background: #efefef;
}
@media (max-width: 46.1875em) {
  .main__content {
    padding: 16px 0;
    text-align: unset;
  }
}
.main__content .content__heading {
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  margin-bottom: 18px;
}
@media (max-width: 46.1875em) {
  .main__content .content__heading {
    font-size: 16px;
  }
}
.main__content .content__paragraph {
  font-size: 16px;
  line-height: 19px;
  max-width: 80%;
}
@media (min-width: 46.25rem) and (max-width: 64rem) {
  .main__content .content__paragraph {
    font-size: 14px;
  }
}
@media (max-width: 46.1875em) {
  .main__content .content__paragraph {
    font-size: 12px;
  }
}
.main__articles {
  display: flex;
  align-items: center;
  gap: 50px;
}
@media (max-width: 63.9375em) {
  .main__articles {
    flex-direction: column;
    gap: 16px;
  }
}
.main__articles .article__heading {
  font-size: 24px;
  line-height: 28px;
  margin-bottom: 18px;
}
@media (max-width: 63.9375em) {
  .main__articles .article__heading {
    font-size: 16px;
    margin-bottom: 12;
  }
}
.main__articles .article__icon {
  width: 128px;
  margin-right: 16px;
  float: left;
}
.main__articles .article__icon > img {
  object-fit: contain;
  max-width: 100%;
  max-height: 100%;
}
@media (min-width: 46.25rem) and (max-width: 64rem) {
  .main__articles .article__icon {
    width: 112px;
  }
}
@media (max-width: 46.1875em) {
  .main__articles .article__icon {
    width: 98px;
  }
}
.main__articles .article__paragraph {
  font-size: 16px;
  line-height: 19px;
}
@media (min-width: 46.25rem) and (max-width: 64rem) {
  .main__articles .article__paragraph {
    font-size: 14px;
  }
}
@media (max-width: 46.1875em) {
  .main__articles .article__paragraph {
    font-size: 12px;
  }
}

.footer {
  border-top: 1px solid #5c5c5c;
  margin-top: 48px;
}
.footer__copyRight {
  padding: 30px var(--paddingSides-content);
  font-size: 16px;
  line-height: 19px;
  color: #3e3e3e;
}
@media (min-width: 46.25rem) and (max-width: 64rem) {
  .footer__copyRight {
    font-size: 14px;
  }
}
@media (max-width: 46.1875em) {
  .footer__copyRight {
    padding: 16px var(--paddingSides-content);
    font-size: 12px;
  }
}